import React from "react"
import { Link } from "gatsby"

function truncateString(str) {
  var num = 1000;
  if (str.length <= num) {
    return str
  }
  return str.slice(0, num) + '...'
}
const PostBlog = ({  url,description,title }) => (
  <Link to={`posts/${url}`} class="postBlog fade">
  <div class="container blurred" style={{paddingBottom:`50px`}}>
    <h4 style={{ fontSize: "20px"}}>{title}</h4>
    <p style={{ fontSize: "17px"}}>{truncateString(description)}</p> 
  </div>
  </Link>
)


export default PostBlog
